* {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  width: 100vw;
}

.nav {
  background-color: #6cba5a;
  padding: 5px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
  min-width: 50vw;
  box-shadow: -1px 4px 20px -6px black;
}

button {
  margin-inline: 7px;
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  font-size: medium;
}

.main {
  flex: 1;
  flex-wrap: wrap;
  padding: 25px;
  background-color: white;
  border-radius: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
  height: 50vh;
  width: 75vw;
  box-shadow: -1px 4px 20px -6px black;
  font-family: "Courier New", Courier, monospace;
  overflow: auto;
}

.main::-webkit-scrollbar {
  background-color: transparent;
}

.footer {
  margin-bottom: 4px;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

/*for toggle switch*/
.switch {
  display: inline-block;
  height: 34px;
  position: relative;
  width: 60px;
}

.switch input {
  display:none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: .4s;
}

.slider:before {
  background-color: #fff;
  bottom: 4px;
  content: "";
  height: 26px;
  left: 4px;
  position: absolute;
  transition: .4s;
  width: 26px;
}

input:checked + .slider {
  background-color: #66bb6a;
}

input:checked + .slider:before {
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}
